import { Component } from '@angular/core';
import { I360FormBuilder } from 'app/utils/validator/form-builder';
import { I360Validators } from 'app/utils/validator/validators';
import { LogChunk } from 'app/utils/log-box/log-box.component';


@Component({
    selector: 'showcase',
    templateUrl: 'showcase.component.html',
    styleUrls: ['showcase.component.scss'],
})
export class ShowcaseComponent {
    form = this.formBuilder.group({
        testInput: this.formBuilder.control('', [I360Validators.commaSeparatedDomains]),
        testTextarea: this.formBuilder.control('', [I360Validators.commaSeparatedDomains]),
        testInputNoLabel: this.formBuilder.control('', [I360Validators.commaSeparatedDomains]),
        testTextareaNoLabel: this.formBuilder.control('', [I360Validators.commaSeparatedDomains]),
    });
    prefixIconEnabled: boolean = true;
    suffixIconEnabled: boolean = true;
    labelEnabled: boolean = true;
    log: LogChunk[] = [];
    constructor(private formBuilder: I360FormBuilder) { }
    loadLog() {
        this.log = [{text: Array.from({length: 100}).map(() => 'long').join('')}];
        setTimeout(() => {
            this.log = this.log.concat(
                [{text: Array.from({length: 20}).map(() => 'log1').join('\n')}]);
        }, 1000);
        setTimeout(() => {
            this.log = this.log.concat(
                [{text: Array.from({length: 20}).map(() => 'log2').join('<br/>')}]);
        }, 2000);
    }
}
